export const Wrapper = styled.div`
  --border: 1px solid ${({ theme }) => theme.color.divider.light};
  border-bottom: var(--border);
  background: ${({ theme }) => theme.color.canvas.secondary};
`;

export const Inner = styled.div`
  display: flex;
  max-width: ${({ theme }) => theme.dimension.pageWidth.laptop}px;
  height: ${({ theme }) => theme.spacing.x4}px;
  align-items: center;
  justify-content: end;
  margin: 0 auto;

  @media ${desktop} {
    max-width: ${({ theme }) => theme.dimension.pageWidth.desktop}px;
  }
`;

const Block = styled.div`
  height: 100%;
  border-right: var(--border);
  border-left: var(--border);
`;

