import { FC } from 'react';

import { Wrapper, Inner, LeftBlock, RightBlock } from './GlobalNavBar.styles';
import { GlobalNavBarProps } from './GlobalNavBar.types';

/**
 * Currently used as the very top bar in the Masthead.
 *
 * It is a full-width bar with two blocks, one on the left and one on the right.
 *
 * The inner content is centred and never exceeds the width of the page
 */
const GlobalNavBar: FC<GlobalNavBarProps> = ({
  leftBlock,
  rightBlock,
  className,
}) => (
  <Wrapper className={className}>
    <Inner>
      <LeftBlock>{leftBlock}</LeftBlock>
      <RightBlock>{rightBlock}</RightBlock>
    </Inner>
  </Wrapper>
);

export default GlobalNavBar;
